import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/style/theme.scss'
import ElementUI from 'element-ui';
import Axios from './axiosConf'
Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(Axios);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
