import Vue from 'vue'
import Router from 'vue-router'
import Login from './pages/Login/Login.vue'

Vue.use(Router)
export const studentsMenu =[
    {
        path: '/main/videoList',
        metaData: {
            title: '党建视频列表',
            icon: 'el-icon-s-home'
        },
        component: () => import(/* webpackChunkName: "videoEidtor" */ './pages/Main/VideoList/VideoList.vue')
    },
    {
        path: '/main/videoEditor',
        metaData: {
            title: '党建内容生成',
            icon: 'el-icon-document'
        },
        component: () => import(/* webpackChunkName: "videoEidtor" */ './pages/Main/VideoEditor/VideoEditor.vue')
    },
    {
        path: '/main/videoInfo',
        metaData: {
            title: '我的视频信息',
            icon: 'el-icon-user-solid'
        },
        component: () => import(/* webpackChunkName: "videoEidtor" */ './pages/Main/VideoInfo/VideoInfo.vue')
    },
    {
        path: '/main/videoLib',
        metaData: {
            title: '我的素材库',
            icon: 'el-icon-film'
        },
        component: () => import(/* webpackChunkName: "videoEidtor" */ './pages/Main/VideoLib/VideoLib.vue')
    },

]
export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/main',
      name: 'main',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.

      component: () => import(/* webpackChunkName: "main" */ './pages/Main/Main.vue'),
      children: [
          {
              path: '',
              redirect:'/main/videoList'
          },
          ...studentsMenu,
          {
              path: '/main/videoPlaying',
              component: () => import(/* webpackChunkName: "videoEidtor" */ './pages/Main/VideoPlay/VideoPlay.vue')
          },
      ]
    }

  ]
})


