import Vue from 'vue'
import Vuex from 'vuex'
import {studentsMenu} from './router'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menus:studentsMenu,
    videoInfo:{}
  },
  mutations: {
    setMenus: (state, menus) => {
      state.menus=menus
    }
  },
  actions: {

  }
})
